html,
body {
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
    Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  background-image: url("./svg/back.png");
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100%;
  font-size: 1px;
}

* {
  box-sizing: border-box;
}

main {
  padding: 20rem 0;
  margin-top: 20rem;
  background: none;
  justify-content: center;
  align-items: center;
}

.footerCss {
  background: white;
  font-size: 100%;
}

.gutter-col {
  padding: 32rem 32rem;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 24rem;
  box-shadow: 0rem 0rem 20rem -12rem rgba(34, 60, 80, 1);
  flex: 1;
  display: grid;
  align-items: flex-start;
  justify-content: center;
  font-size: 1.5rem;
  font-weight: 500;
  font-family: "Nunito Sans", Sans-serif;
  text-align: left;
  grid-template-rows: 0fr 0fr;
  gap: 1vw;
}

.gutter-col span {
  font-size: 1rem;
  font-weight: 500;
}

.gutter-row {
  display: flex;
  flex-wrap: wrap;
  gap: 40rem;
  justify-content: center;
}

.gutter-row-2 {
  display: flex;
  gap: 20rem;
  justify-content: center;
}

.ant-menu-horizontal .ant-menu-item-selected::after {
  bottom: 10rem !important;
}

.ant-menu-horizontal .ant-menu-item::after {
  bottom: 10rem !important;
}

.ant-menu-horizontal .ant-menu-item:hover::after {
  bottom: 10rem !important;
}

.gutter-header-col {
  position: relative;
  overflow: hidden;
  border-radius: 11rem;
  padding: 20rem;
  padding-left: 70rem;
  padding-right: 70rem;
  max-width: 630rem;
}

.gutter-header-col::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000000;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0.45;
  z-index: 0;
}

.gutter-box-col {
  position: relative;
  overflow: hidden;
  width: 100%;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  gap: 20rem;
  padding-top: 20rem;
  padding-bottom: 20rem;
  justify-content: center;
}

.gutter-box-col::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000000;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0.45;
  z-index: 0;
}

.content-header-col {
  position: relative;
  z-index: 1;
}

.gutter-header-col-2 {
  position: relative;
  overflow: hidden;
  border-radius: 11rem;
  padding: 20rem;
  padding-left: 2vw;
  padding-right: 7vw;
  max-width: 630rem;
  margin-top: 20rem;
  margin-bottom: 20rem;
}

.gutter-header-col-2::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000000;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0.4;
  z-index: 0;
}

.mediaIMG {
  border: 1rem solid black;
  box-shadow: 0rem 0rem 3rem 1rem rgba(34, 60, 80, 0.57);
}

.ColGap {
  display: grid;
  gap: 1.5vw;
}

#hoverIcon :hover {
  -webkit-transition: all 0.6s cubic-bezier(0.18, 0.89, 0.32, 1.28);
  transition: all 0.6s cubic-bezier(0.18, 0.89, 0.32, 1.28);
  opacity: 0.5;
  cursor: pointer;
}

.gutter-box-footer {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 25vh;
}

.gutter-box-footer::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url(./svg/footerBack.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  filter: brightness(0.5);
  z-index: 0;
}

.gutter-box-protez {
  position: relative;
  overflow: hidden;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 40rem;
  justify-content: center;
}

.gutter-box-protez::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url(./svg/ProtezBack.png);
  background-position: bottom;
  background-size: cover;
  background-repeat: no-repeat;
  filter: brightness(0.5);
  z-index: 0;
}

/* .ant-card-body {
  padding: 0 !important;
} */

@media (max-width: 1100px) {
  .imgMediaSert {
    height: 500rem !important;
  }
}

@media (max-width: 400px) {
  .mediaIMG {
    width: 200rem;
  }
}

@media (max-width: 900px) {
  #mediaGoal {
    font-size: 15rem !important;
    padding-left: 40rem !important;
  }
}

@media (max-width: 400px) {
  #mediaGoal {
    font-size: 12rem !important;
    padding-left: 40rem !important;
  }
}

@media (max-width: 350px) {
  #mediaGoal {
    font-size: 10rem !important;
    padding-left: 40rem !important;
  }
}

/* ///////////////////Меняем шрифт корневого элемента с пиксельного на адаптивный, для сохранения пропорциональности блоков с fullHd на 2Ка и 4Ка////////////////////////// */
@media (min-width: 1920px) {
  html {
    font-size: 0.0521vw;
  }
}
